import React from 'react'
import { GatsbyImage, getImage } from "gatsby-plugin-image"

import './index.scss'

export const Bio = ({ author }) => {
  const image = getImage(author.profilepicture);

  return (
    <div className='author-bio'>
      <div className='author-bio__avatar-and-name'>
        <GatsbyImage className='author-bio__avatar'
               image={image}
               alt={author.name}
        />
        <h1 className='author-bio__name'>
          <span className='author-bio__name--firstname'>{author.name.split(' ')[0]}</span> {author.name.substring(author.name.split(' ')[0].length)}
        </h1>
      </div>
      <p>{author.bio}</p>
    </div>
  )
}

export default Bio
