// templates/Author/index.jsx
import React from 'react'
import { Bio } from '../components/bio'
import { Layout } from '../layout'
import { graphql } from 'gatsby'
import { BlogContent } from '../components/blog-content'
import { useRenderedCount } from '../hooks/useRenderedCount'
import { useScrollEvent } from '../hooks/useScrollEvent'
import * as Dom from '../utils/dom'
import * as EventManager from '../utils/event-manager'
import './author.scss'

const BASE_LINE = 80
const Author = ({
                  data: {
                    site: {
                      siteMetadata: { title, configs },
                    },
                    authorYaml,
                    allMarkdownRemark: { edges: postNodes },
                  },
                  location,
                }) => {
  function getDistance(currentPos) {
    return Dom.getDocumentHeight() - currentPos
  }

  const { countOfInitialPost } = configs
  const [count, countRef, increaseCount] = useRenderedCount()
  useScrollEvent(() => {
    const currentPos = window.scrollY + window.innerHeight
    const isTriggerPos = () => getDistance(currentPos) < BASE_LINE
    const doesNeedMore = () =>
      postNodes.length > countRef.current * countOfInitialPost

    return EventManager.toFit(increaseCount, {
      dismissCondition: () => !isTriggerPos(),
      triggerCondition: () => isTriggerPos() && doesNeedMore(),
    })()
  })
  return (
    <Layout location={location}
            title={title}>
      <div className='author-profile'>
        <div className='author-profile__bio'>
          <Bio author={authorYaml} />
        </div>
        <BlogContent
          showAll={true}
          posts={postNodes}
          countOfInitialPost={countOfInitialPost}
          count={count}
          category={'All'}
          postsVisible={true}
        />
      </div>
    </Layout>
  )
}

export default Author;

export const pageQuery = graphql`
    query PostsByAuthorId($authorId: String!) {
        site {
            siteMetadata {
                title
                configs {
                    countOfInitialPost
                }
            }
        }
        allMarkdownRemark(
            sort: { fields: [frontmatter___date], order: DESC }
            filter: { fields: { authorIds: { eq: $authorId } }, frontmatter: {  type: { eq: "blog" } } }
            limit: 5
        ) {
            edges {
                node {
                    id
                    frontmatter {
                        title
                        category
                        image {
                            childImageSharp {
                              gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED)
                            }
                        }
                        date
                        type
                        tags
                        authors {
                            id
                            name
                        }
                    }
                    fields {
                        authorIds
                        slug
                    }
                }
            }
        }
        authorYaml(id: { eq: $authorId }) {
            id
            name
            bio
            profilepicture {
                childImageSharp {
                  gatsbyImageData(width: 500, height: 500)
                }
            }
            github
            linkedin
            email
            phone
        }
    }
`
